.header {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-left: 340px;
    padding-right: 60px;
    padding-top: 50px;
    z-index: 9;

    &__menu {
        font-size: 24px;
        color: #2196b4;
        text-transform: uppercase;
    }

    &__right {
        display: flex;
        justify-content: center;
        // align-items: center;
    }

    &__contacts {
        display: flex;
        flex-direction: column;

        a {
            color: #2196b4;
            text-transform: uppercase;
        }
    }

    &__btn {
        background-color: #2196b4;
        font-size: 20px;
        padding: 10px 20px;
        cursor: pointer;
        text-transform: uppercase;
        margin-left: 40px;
        transition: all .3s ease;

        &:hover {
            background-color: #1f859e;
        }
    }
}

.dropdown {
    display: flex;
    color: #2196b4;
    font-size: 24px;
    text-transform: uppercase;    
    flex-direction: column;
    margin-left: 90px;
    margin-top: 40px;
    
    a + a {
        margin-top: 25px;
    }
    transition: all .3s ease;

    a {
        &:hover {
            color: #1f859e;
        }
    }
}